<template>
  <!--
  左侧固定宽，右侧自适应屏幕宽；
  左右两列，等高布局；
  左右两列要求有最小高度，例如：200px;（当内容超出200时，会自动以等高的方式增高）
  要求不用JS或CSS行为实现；
  原文: https://www.w3cplus.com/css/two-cloumn-width-one-fixed-width-one-fluid-width -->
  <div id="container">
    <component :is="layoutSide" id="left" v-bind:style="{minHeight: this.$store.getters.minHeight+ 'px', width: this.$store.getters.sideWith + 'px' }"></component>
    <component :is="layoutHeader" id="header" v-bind:style="{marginLeft: this.$store.getters.sideWith + 'px' }"></component>
    <div id="content"  v-bind:style="{minHeight: this.$store.getters.minHeight+ 'px', marginLeft: this.$store.getters.sideWith + 'px' }">
      <router-view class="el-row" v-bind:style="{minHeight: this.$store.getters.minHeight+ 'px'}"> </router-view>
      <el-row id="footer">
        <component :is="layoutFooter"></component>
      </el-row>
    </div>
  </div>
</template>
<style scoped>
#container {
  overflow: hidden;
}
#left {
  background: #ccc;
  float: left;
  /* width: 200px;*/
  margin-bottom: -99999px;
  padding-bottom: 99999px;
}
#content {
  background: #eee;
  /* margin-left: 200px;*/
  margin-bottom: -99999px;
  padding-bottom: 99999px;
}
</style>
<script>
export default {
  name: 'Layout',
  components: {
  },
  beforeRouteEnter: (to, from, next) => {
    var currentLayoutHeader
    var currentLayoutSide
    var currentLayoutFooter

    // 永远保留最底层的meta数据(Layout不允许嵌套使用)
    to.matched.forEach(record => {
      if (record.meta.layoutHeader) {
        currentLayoutHeader = record.meta.layoutHeader
      }
      if (record.meta.layoutSide) {
        currentLayoutSide = record.meta.layoutSide
      }
      if (record.meta.layoutFooter) {
        currentLayoutFooter = record.meta.layoutFooter
      }
    })
    next(vm => {
      // 通过 `vm` 访问组件实例
      vm.setLayout(currentLayoutHeader, currentLayoutFooter, currentLayoutSide)
    })
  },
  props: {

  },
  data() {
    return {
      layoutHeader: '',
      layoutSide: '',
      layoutFooter: ''
    }
  },
  computed: {

  },
  watch: {

  },
  created() {

  },
  mounted() {

  },
  methods: {
    setLayout(layoutHeader, layoutFooter, layoutSide) {
      this.layoutHeader = layoutHeader
      this.layoutFooter = layoutFooter
      if (layoutSide) {
        this.layoutSide = layoutSide
      } else {
        this.$store.commit('setSideWith', 0)
      }
    }
  }
}
</script>



